
import XHeader from 'vux/src/components/x-header/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


export default {
name: 'Layout',
directives: { TransferDom },
components: { XHeader, Drawer, Actionsheet, Group, Cell, ViewBox },
data () {
return {
cachedList: ['Home', 'Portfolio', 'Videos', 'About'],
menus: {
zh: '中文',
en: 'EngLish'
},
drawerVisibility: false,
showMenuIcon: true,
showMenus: false
}
},
computed: {
headerTitle () {
switch (this.$route.name) {
case 'Home':
return '主页'
case 'Portfolio':
return '广告设计作品'
case 'Words':
return '我的文章'
case 'Videos':
return '我的视频'
case 'About':
return '我的简历'
case 'Content':
return '文章详情'
default:
return ''
}
}
},
watch: {
'$route.name' (value) {
if (value === 'Content') {
this.showMenuIcon = false
} else {
this.showMenuIcon = true
}
}
},
created () {
if (this.$route.name === 'Content') {
this.showMenuIcon = false
}
},
methods: {
onClickMenu (key) {
console.log(key)
}
}
}
